/* ==========================================================================
   Header
   ========================================================================== */

#about img {
  max-width: 200px;
  position: relative;
  right: 35px;
}

.about-links {
  margin-left: 10%;
  float: right;
}

@media screen and (max-width: 550px) {
  .about-links + p {
    clear: both;
    width: 100%;
    display: block;
  }
}

.about-img {
  margin-bottom: 40px;
  position: relative;
  &:hover {
    &:before {
      border: 14px solid $violet;
    }
  }
  &:before {
    box-sizing: border-box;
    content: "";
    position: absolute;
    bottom: -9px;
    right: 8px;
    border: 14px solid $cyan;
    width: 200px;
    height: 200px;
    z-index: -1;
    border-radius: 100px;
    transition: .5s ease-out;
  }
}

.project-psd:hover {
  color: $pink;
}

.about-list {
  li {
    position: relative;
    display: block;
    font-size: 1.25em;
    font-weight: $font-weight-medium;
    line-height: 1.3;
  }
}

.about-list:not(.cut-half) {
  li:before {
    color: $violet;
    content: "\2022";
    position: absolute;
    top: -6px;
    left: -10px;
    font-size: 27px;
    margin-left: -7px;
  }
}

.cut-half li:before {
  color: $violet;
  content: "\2022";
  position: absolute;
  bottom: -4px;
  left: -10px;
  font-size: 27px;
  margin-left: -7px;
}

br:not(.line-date) {
  @media screen and (min-width: 480px) {
    display: none;
  }
}

.cut-half {
  position: relative;
  left: -20px;
  columns: 2;
  @media screen and (min-width: 480px) {
    columns: 1;
  }
  @media screen and (min-width: 630px) {
    columns: 2;
  }
  li {
    margin-left: 20px;

  }
}

.scholarship {
  padding-top: 10px;
}

.cyan-link {
  position: relative;
  display: inline-block;
  padding: 0 3px;
  font-weight: bold;
  & span {
    top: 7px;
  }
  margin-bottom: 1em;
}
